const validationCustomer = (value) => {
  const errors = {};
  if (!value.namaCustomer) {
    errors.namaCustomer = "Tidak Boleh kosong";
  }
  if(!value.kodeCustomer){
    errors.kodeCustomer = "Tidak Boleh Kosong";
  }
  if (!value.namaToko) {
    errors.namaToko = "Tidak Boleh kosong";
  }
  if(!value.level_customer){
    errors.level_customer = "Tidak Boleh Kosong";
  }
  if (!value.kodeArea) {
    errors.kodeArea = "Tidak Boleh kosong";
  }
  if (!value.kodeKota) {
    errors.kodeKota = "Tidak Boleh kosong";
  }
  if (!value.kodeProvinsi) {
    errors.kodeProvinsi = "Tidak Boleh kosong";
  }
  if (!value.kodeKecamatan) {
    errors.kodeKecamatan = "Tidak Boleh kosong";
  }
  return errors;
};

export default validationCustomer;
